import React from "react";
import {ConvertCardInterface} from "../../interface/start_mining";
import styled from "styled-components";
import {ButtonWidget, InputWidget, SpacingWidget, TransactionModal, ValueWidget} from "../base";
import {WidthStyle} from "../style/width_style";
import {useForm} from "react-hook-form";
import {ModalHeaderWidegt} from "./modal_header_widget";
import {CardStyle} from "../style/card_style";
import {useWait} from "../../utils/wait_fn";
import {ModalProps} from "../../provider/model_provider";
import balancerImage from "../../assets/imgs/balancer.png"


interface ConvertInterface extends ModalProps{
    confirmationFn:(ev:number)=> Promise<any>,
    title:string,
    url:string,
    buttonText:string,
    max?:string | number,
    label?:string
}

export const ConvertMoal:React.FC<ConvertInterface> = ({label = '',max ='',onDismiss,url,title,buttonText,confirmationFn}) =>{
    const {register,handleSubmit,  setValue, errors} = useForm();


    const {request,status, data} = useWait(confirmationFn);

    return (
        <TransactionModal onDismiss={onDismiss} txHash={data} status={status}>
            <ConvertStyle>
                <ModalHeaderWidegt url={url || balancerImage} title={"NSURE NETWORK"} />

                <ConverBottomStyle>
                    <AvailableStyle bgColor={"rgba(20, 28, 78, 1)"}>
                        <h2>{title}</h2>
                        <h4><ValueWidget value={max} decimals={3}/></h4>
                    </AvailableStyle>
                    <SpacingWidget />
                    <form>
                        <InputWidget
                            setValue={(ev) => setValue(ev, max)}
                            label={label}
                            placeholder={"Input"}
                            name={'amount'}
                            errors={errors}
                            inputRef={register({
                                required: 'no Enter the number is dame',
                                max: {value: max, message: 'Enter the number'},
                            })}
                        />


                        {/*   <ButtonWidget text="Get Quote" type="img" onClick={handleSubmit(handleLogin)}/>*/}
                        {/*<ButtonWidget />*/}
                    </form>
                    <SpacingWidget />

                    <ButtonWidget text={buttonText} type="img" onClick={handleSubmit((data)=>request(data.amount))}/>
                </ConverBottomStyle>
            </ConvertStyle>
        </TransactionModal>
      );
}


const AvailableStyle = styled.div`
${CardStyle.BorderTwoStyle};
height: 118px;
display: flex;
flex-flow: column;
justify-items: center;
align-items: center;
justify-content: center;
h2{
font-size: 14px;
font-weight: 400;
color: #FFFFFF;
}
h4{
font-size: 36px;
font-weight: 500;
color: #FFFFFF;
}
`

const ConverBottomStyle = styled.div`
padding: 30px;
box-sizing: border-box;
background-color: RGBA(22, 31, 87, 1);
`


const ConvertStyle = styled.div`
width: 100vw;
max-width: 420px;
`

