import React, {FunctionComponent, ReactNode, useCallback, useContext, useEffect, useMemo, useState} from "react";
import styled from "styled-components";


import NetAxios from "../../net/api"
import {CoversStyle} from "../covers/style";
import {CircularProgress} from "@material-ui/core";
import {ImagelabelWidget} from "../base";
import {SearchContext} from "./search_list";
import {useSearchFn} from "./search_fn";
import {ItemCellWidget} from "../base/list_main_widget";







export const HttpListMain: React.FC<{structure: (ev:any) => CellStructure[]}> = ({structure }) => {

   const [faceCallBack] = useSearchFn();
   const {listCellList,path,loading} = useContext(SearchContext);




    useEffect(()=>{
        faceCallBack(123);
    },[path]);



    return (
        <div>
            {
                !loading && listCellList.length == 0? <EmptyStyle>N/A</EmptyStyle>: listCellList.map((ev, index) => <ItemCellWidget key={'Cell' + index}   model={structure(ev)}/>)
            }
            {

                loading ?  <LoadginStyle><CircularProgress /></LoadginStyle>: <div />
            }
        </div>
    );
}







export interface CellStructure {
    inner: string,
    url?: string,
    type?: 'Imagelabel' | 'text'|'grade' | null,
    grade?:number,
    size?: number,
    color?: string,
    onClick?: () => void,
    children?:ReactNode,
    textAlign?: "inherit" | "-moz-initial" | "initial" | "revert" | "unset" | "center" | "end" | "justify" | "left" | "match-parent" | "right" | "start"
}


const EmptyStyle = styled.h2`
    color: white;
  text-align: center;
  padding: 10px 0;
`



const LoadginStyle = styled.div`
 
    margin: 0 auto;
  padding: 40px 0;
  display: flex;
  justify-content: center;
`