import React, {useCallback, useContext, useState} from "react";
import {ButtonWidget, SpacingWidget} from "../../base";
import {SearchListWidget} from "../../search_list/search_list";
import {usePostStakeStake} from "../../../view/stake_list/fn/post_stake_stake";
import {formatAddress} from "../../../utils/formatBalance";
import {FormattedInputs} from "./formatted_input";
import {inputMapType} from "../../../utils/max";
import {ModalContext} from "../../../provider/model_provider";
import {ProductInformation} from "../../modal/product_information_widget";
import styled from "styled-components";





export const BottomWidget: React.FC = () => {

    const [handlePostStakeStake] = usePostStakeStake()
    const [inputMap,setInputMap] = useState<inputMapType>({});



    const { onPresent} = useContext(ModalContext);


    return (
        <>
            <SearchListWidget url={"/stake/all"} title={"All Markets"}
                              sub={"Find and select the contract you want to cover."}
                              structure={(ev) => [
                                  {
                                      inner: ev.name,
                                      url: ev.avatar,
                                      color: "#FFFFFF",
                                      type: 'Imagelabel',
                                      size: 28
                                  },
                                  {
                                      inner: formatAddress(ev.address),
                                      color: "#FFFFFF",
                                  },
                                  {
                                      inner: ev.riskLevel.toString(),
                                      color: "#FFFFFF",
                                      children: <FormattedInputs inputMap={inputMap} setInputMap={setInputMap} address={ev.address}/>
                                  },
                                  {
                                      inner: ev.riskLevel.toString(),
                                      color: "#FFFFFF",
                                      type: 'grade',
                                      grade: ev.riskLevel,
                                      onClick:() =>{
                                          onPresent(<ProductInformation imgName={ev.avatar} nick={ev.name} />)
                                      }
                                  },
                                  {
                                      inner: ev.premiumRate,
                                      color: "#FFFFFF",
                                  },
                              ]}>

                <div>Project Name</div>
                <div>Contract Address</div>
                <div>Staking</div>
                <div>Risk Level</div>
                <div>Current Premium Rate</div>


            </SearchListWidget>



            <ButtonStyle>
                <ButtonWidget text={'Confirm'} type={"img"} onClick={() => handlePostStakeStake(inputMap)}/>
            </ButtonStyle>
        </>
    );
}


const ButtonStyle = styled.div`
    display: flex;
  padding: 30px;
  box-sizing: border-box;
  justify-content: flex-end;
  
`
