




import React, {PureComponent, useCallback, useContext, useEffect, useMemo, useState} from "react";
import {Tooltip, Legend, Area, AreaChart} from 'recharts';


const data = [
    {name: 'Page 1', uv: 4000, pv: 2200, amt: 2410},
    {name: 'Page 2', uv: 3000, pv: 2010, amt: 2220},
    {name: 'Page 3', uv: -1000, pv: 2090, amt: 2300},
    {name: 'Page 4', uv: 500, pv: 1800, amt: 2010},
    {name: 'Page 5', uv: 500, pv: 1800, amt: 2010},
    {name: 'Page 6', uv: -250, pv: 2300, amt: 2510},
    {name: 'Page 7', uv: 3490, pv: 1900, amt: 2110},
    {name: 'Page 8', uv: 3490, pv: 1900, amt: 2110},
    {name: 'Page 9', uv: 3490, pv: 1900, amt: 2110},
    {name: 'Page 10', uv: 3490, pv: 1900, amt: 2110},
    {name: 'Page 11', uv: 3490, pv: 1900, amt: 2110},
];

export const ChartWidget: React.FC = () => {

    const c1 = "rgba(238, 35, 136, 1)";
    const c2 = "rgba(238, 35, 136, 0)";
    let end = 1 / (data.length -1) *100;
    let ArrWidget =  useMemo(()=>{
        let arr:any = [];
        data.map((ev,index) => {
                let start = index / (data.length -1) *100;
                if(index+1 !== data.length){
                    arr.push(<stop key={index+'a'} style={{stopColor: c1,}} offset={start + '%'} />);
                    arr.push(<stop key={index+'b'} style={{stopColor: c2}} offset={(start+end) + '%'} />);
                }

        });

        return arr;
    },[])
    return (
    <AreaChart width={1200} height={400} data={data} margin={{top: 20, right: 20, bottom: 20, left: 20}}>

        <Tooltip/>

        <defs>
            <linearGradient id="splitColors" x1="0" y1="0" x2="0" y2="100%">
            <stop  style={{stopColor: c1,}}  offset={'0%'} />
            <stop  style={{stopColor: c2,}} offset={'70%'} />
            </linearGradient>

        </defs>
        <Area type="monotone" dataKey='amt' fill="url(#splitColors)" stroke='#EE2388'/>


    </AreaChart>);

  
};
