import React from "react";
import styled from "styled-components";
import {BuyListMain, ListTopWidget} from "../../widget/covers";
import {ListCell} from "../../widget/covers/buy/buy_list_main";
import {ListCellMode} from "../../interface/covers_interface";
import {DateUtils} from "../../utils/date";
import {BorderWidget, ButtonWidget, SpacingWidget} from "../../widget/base";
import {BgWidget} from "../../widget/start_mining/stake_to_mine/bg_widget";
import {ItemStyles} from "../../widget/style/item_style";
import {WidthStyle} from "../../widget/style/width_style";
import {CardStyle} from "../../widget/style/card_style";
import {useModel} from "../../hook/use_model";
import {ConfirmationModal} from "../../widget/modal/confirmation_widget";
import {ConvertMoal} from "../../widget/modal/convert_xxx";

export const WithdrawPage: React.FC = () => {

    const [handlePresent] = useModel(
        <ConvertMoal title={'Do Withdraw'}
                     url={'model.url'}
                     buttonText={"Do Withdraw"}
                     confirmationFn={(ev) => {
                         return new Promise((res, err) => {
                             setTimeout(() => {
                                 res('dasd')
                             }, 2 * 1000);
                         })
                     }}/>
    );
    return (
        <div>
            <WithdrawStyle>
                <BorderWidget y={10}>
                    <h2>Stake to mine Nsure</h2>

                </BorderWidget>
                <BgWidgetStyle size={{all: 30}}>
                    <BgWidget>
                        <ItemStyle>
                            <h3>Total nETH Locked</h3>
                            <h4>3,512.2</h4>

                        </ItemStyle>
                        <div className={'line'}/>
                        <ItemStyle>
                            <h3>Total nETH Available for Withdraw</h3>
                            <h4>
                                15,666.0
                            </h4>
                            <div>
                                <ButtonWidget text={"Withdraw"} onClick={handlePresent} type={"img"}/>
                            </div>
                        </ItemStyle>
                    </BgWidget>
                </BgWidgetStyle>


            </WithdrawStyle>
            <SpacingWidget/>
            <WithdrawStyle>
                <MyListWidget/>
            </WithdrawStyle>

        </div>

    )
}


const MyListWidget: React.FC = () => {
    return (
        <>
            <ListTopWidget>
                <div>Time</div>
                <div>Amount</div>
                <div>Pool</div>
                <div>Address</div>
                <div>Unlock in</div>
                <div className={'start'}>Action</div>
            </ListTopWidget>
            <BuyListMain listCell={ListCell} structure={(ev: ListCellMode) => [
                {
                    inner: ev.nick,
                    url: ev.avatar,
                    color: "#FFFFFF",
                },
                {
                    grade: ev.grade,
                    inner: `Level ${ev.grade}`,

                },
                {
                    inner: ev.amount.toString() + 'ETH',
                    color: "#FFFFFF",
                },
                {
                    inner: `${DateUtils.dataString(ev.startDate)} -- ${DateUtils.dataString(ev.endDate)}`,
                    color: "rgba(39, 157, 244, 1)",
                },
                {
                    inner: `12-Oct-2020`,
                    color: "#FFFFFF",
                },
                ev.grade < 3 ?
                    {
                        inner: `12-Oct-2020`,
                        color: "#FFFFFF",
                        children: <div style={{width: '80%'}}>
                            <ButtonWidget text={'Unstake'} type={"img"}/>
                        </div>
                    } : {
                        inner: `Unstaked`,
                        color: "rgba(39, 157, 244, 1)",
                    },
            ]}/>
        </>
    );
}


const BgWidgetStyle = styled.div`
  ${WidthStyle.PaddingSize}
`

const ItemStyle = styled.div`
  ${ItemStyles.upDown};

  > div {
    width: 30%;
    margin: 10px auto 0 auto;
  }
`


const WithdrawStyle = styled.div`
  ${CardStyle.wap}
`
