import React from "react";
import styled from "styled-components";
import {ListCellMode} from "../../../interface/covers_interface";
import {ButtonWidget, ImageWidget, TransactionModal} from "../../base";
import {Grid} from "@material-ui/core";
import {useWait} from "../../../utils/wait_fn";
import bgImg from "../../../assets/imgs/cover_modal_bg_top.png";
import {useBuyInsurance} from "../../../server/buy_server";

interface CoverInterface{
    unit:string,
    address:string,
    fixedPayout:string,
    days:number,
    cover:string,
    r:string,s:string,v:string,
    deadline:number,
}

export const CoverModal:React.FC<{Cover:CoverInterface,CellModel:ListCellMode, onDismiss?: () => void}> = ({Cover,CellModel,onDismiss}) =>{


    const [buyInsuranceWithETH] = useBuyInsurance();

    const {request,status,data} = useWait(() => {
       return buyInsuranceWithETH({
            ...Cover,
            ...CellModel,
        })
    });

    return (
        <TransactionModal status={status} txHash={data} onDismiss={onDismiss}>
            <ImageStyle>
                <ImageWidget size={90} url={CellModel.avatar} />
            </ImageStyle>
            <CoverModalMain>
                <CoverTop>
                    <h3>{CellModel.nick} nick</h3>
                    <h4>
                        {/*To Supply or Repay Dai to the Compound Protocol,you*/}
                        {/*need to enable it first.*/}
                    </h4>
                </CoverTop>
                <CoverBottomStyle>
                    <MsgCellStyle>
                        <h3>Cover Amount</h3>
                        <h4><p>{Cover.fixedPayout}</p><span>ETH</span></h4>
                    </MsgCellStyle>

                    <MsgCellStyle>
                        <h3>Cover Period</h3>
                        <h4><p>{Cover.days}</p><span>DAYS</span></h4>
                    </MsgCellStyle>

                    <MsgCellStyle>
                        <h3>Smart Contrsct Address</h3>
                        <h4><p>{Cover.address}</p><span>{Cover.unit}</span></h4>
                    </MsgCellStyle>



                    <MsgCellStyle>
                        <h3>Cost of Cover</h3>
                        <h4><p>{Cover.cover}</p><span>{Cover.unit}</span></h4>
                    </MsgCellStyle>

                    <BottomStyle>
                        <Grid container justify={"space-around"}>
                            <Grid item md={3}>
                                <ButtonWidget type={"border"} text={"Close"} onClick={onDismiss}/>
                            </Grid>
                            <Grid item md={3}>
                                <ButtonWidget type={"img"} text={"Confirm"} onClick={request}/>
                            </Grid>
                        </Grid>
                    </BottomStyle>
                </CoverBottomStyle>
            </CoverModalMain>
        </TransactionModal>
    );
}


