import {useCallback, useContext} from "react";
import NetAxios from "../../net/api";
import {SearchContext} from "./search_list";
import {HttpServer} from "../../net/http_server";


export const useSearchFn = ()=>{
    const {setListCellList,path,setLoading} = useContext(SearchContext);

    const  faceCallBack = useCallback(async (page:number) =>{

        try{
            const data:any =  await NetAxios.get(path,{ user:HttpServer.account,});
            setListCellList(data);
            setLoading(0);
        }catch (e) {
            console.log(e);
        }

    },[setLoading,setListCellList,path]);

    return [faceCallBack];
}