import React, {createContext, useCallback, useState} from "react";
import {ListHeader, ListTopWidget} from "./list_header";
import {CellStructure, HttpListMain} from "./http_list";


interface SearchListInterface {
    structure: (ev: any) => CellStructure[],
    title: string,
    sub: string,
    url:string,
    isShowSearch?:boolean
}

export const SearchListWidget: React.FC<SearchListInterface> = ({
                                                                    sub,
                                                                    title,
                                                                    structure,
                                                                    url,
                                                                    isShowSearch = true,
                                                                    children
                                                                }) => {

    const [listCellList,setListCellList] = useState([]);
    const [loading,setLoading] = useState(1);
    const [path,setPath] = useState(url)

    return (
        <SearchContext.Provider value={{
            path,
            setPath,
            listCellList,
            setListCellList,
            loading,setLoading
        }}>
            <ListHeader title={title} sub={sub} isShowSearch={isShowSearch}/>
            <ListTopWidget>{children}</ListTopWidget>
            <HttpListMain structure={structure}/>
        </SearchContext.Provider>
    );
}







export const SearchContext = createContext<SearchInterfacer>({
    path:'',
    setPath: () =>{},
    listCellList:[],
    setListCellList:() => {},
    loading:1,
    setLoading:() => {}
});

interface SearchInterfacer{
    path:string,
    setPath:Function,
    listCellList:any[],
    setListCellList:Function,
    loading:number,setLoading:Function

}