export const MyCoversPage:React.FC = () =>{
    return (<CoversProvider>
        <ChilderWidget />
    </CoversProvider>);
}

const ChilderWidget:React.FC = () =>{

    return (
        <div style={{minHeight:"64vh"}}>
            <ListWapStyle>
                <BorderWidget>
                        <h2>Nsure Staking</h2><ButtonWidget to="/cover/all" text="My Cover" type="img"/>
                </BorderWidget>
                <NsuerStakingWidget />
            </ListWapStyle>

            <div style={{height:'29px'}} />
            <ListWapStyle>
                <MyListWidget />

            </ListWapStyle>

        </div>
    )
}



const MyListWidget: React.FC = () => {
    const { onPresent} = useContext(ModalContext);
    return (
        <SearchListWidget  url={"/list"} title={"All Markets"} sub={"Find and select the contract you want to cover."} structure={(ev) => [
            {
                inner: ev.nick,
                url: ev.avatar,
                color: "#FFFFFF",
                type: 'Imagelabel',
                size: 28
            },
            {
                grade: 0,
                type:'grade',
                inner:`Level ${1}`,
                color: theme.gradeColor[ev.grade||1],
                onClick:() =>{
                    onPresent(<ProductInformation imgName={ev.avatar} nick={ev.nick} />)
                }
            },
            {
                inner: ev.amount + "ETH",
                color: "#FFFFFF",
            },
            {
                inner: '',
                type:null,
                children:  <div><ButtonWidget key={ev.id} to={"/cover/average_premium?data="+JSON.stringify(ev)} text="Quote" type="color"/></div>

            }
        ]}>
            <div>Market</div>
            <div>Risk Level</div>
            <div>Cover Amount</div>
            <div />
        </SearchListWidget>
    );
}






const ListWapStyle = styled.div`
background-color:#161F57;
border-radius:0 4px 4px 4px;
overflow: hidden;
`;








