import React, {useState} from "react";
import TextField from "@material-ui/core/TextField";
import NumberFormat from "react-number-format";
import {inputMapType, useMaxAmount} from "../../../utils/max";
import styled from "styled-components";
import {ButtonWidget} from "../../base";
import {Button} from "@material-ui/core";


export const FormattedInputs: React.FC<{ setInputMap: Function, address: string, inputMap: inputMapType }> = ({
                                                                                                                  setInputMap,
                                                                                                                  address,
                                                                                                                  inputMap
                                                                                                              }) => {

    const [values, setValues] = React.useState('');

    const handleChange = (event: string) => {
        setValues(event);
        setInputMap({
            ...inputMap,
            [address]: event
        });
    };
    const [max,setMax] = useState(0);

    const handleMax = useMaxAmount();

    return (
        <FormattedStyle>
            <div className={'_input'}>
                <TextField
                    value={values}
                    onChange={(ev) => handleChange(ev.target.value)}
                    onFocus={(ev) => {
                        console.log()
                        setMax(handleMax(inputMap, address));
                    }}
                    InputProps={{
                        inputComponent: NumberFormatCustom as any,
                    }}
                />
            </div>


        </FormattedStyle>
    );
}


const FormattedStyle = styled.div`
        display: flex;
  width: 80%;
  padding: 0 5%;
  box-sizing: border-box;
  ._input {
    background-color: #162C75;
    border-radius: 5px;
    padding: 4px;
    box-sizing: border-box;
  }
`


interface NumberFormatCustomProps {
    inputRef: (instance: NumberFormat | null) => void;
    onChange: (event: { target: { name: string; value: string } }) => void;
    name: string;
}

function NumberFormatCustom(props: NumberFormatCustomProps) {
    const {inputRef, onChange, ...other} = props;

    return (
        <NumberFormat
            {...other}
            getInputRef={inputRef}
            onValueChange={(values) => {
                onChange({
                    target: {
                        name: props.name,
                        value: values.value,
                    },
                });
            }}
            thousandSeparator
            isNumericString
            prefix=""
        />
    );
}



