import {useWeb3} from "../provider/web3_provider";
import {useCallback} from "react";
import {useWallet} from "use-wallet";
import {Contract} from "web3-eth-contract";
import {useSendFn} from "../utils/web3_send";
import {HttpServer} from "../net/http_server";
import {numberToUint256} from "../utils/formatBalance";


export const useConvert = (contract: Contract | undefined, address: string) => {


    const handleSendFn = useSendFn(address);
    const handleConvert = useCallback(async (amount: number) => {
        if(!contract) return ;
        let method = contract.methods.convert();
       let data =  await handleSendFn(method, numberToUint256(amount).toFixed());
       return data.transactionHash

    }, [contract]);


    return handleConvert

}