import styled from "styled-components";
import React, {useContext} from "react";
import {CoversContext} from "../../../provider/covers_provider";
import {AliasEnum, TabCellModel} from "../../../interface/covers_interface";
import {Link, NavLink} from "react-router-dom";


const TabData:Array<TabCellModel> = [
    {
        name:'Active Covers',
        alias:AliasEnum.Active,
    },
    {
        name:'Inactive Covers',
        alias:AliasEnum.Inactive,
    },
    {
        name:'Claims',
        alias:AliasEnum.Claims,
    },
    {
        name:'Policy Mining',
        alias:AliasEnum.Rewards,
    },
]

export const TabWidget:React.FC = ()=>{
    return (
        <TabStyle>
            {
                TabData.map((ev)=>{
                    return <TabItem key={ev.name} model={ev}/>;
                })
            }
        </TabStyle>
    );
}

const TabItem:React.FC<{model:TabCellModel}> = ({model})=>{

    const {setAlias,alias} = useContext(CoversContext);

    return (<TabCellStyle activeClassName={'ac'} to={`/cover/all/${model.alias}`} onClick={() => setAlias(model.alias)}>
        {model.name}
    </TabCellStyle>);
}



const TabStyle = styled.div`
display: flex;
justify-content: center;
  align-items: center;

`;


const TabCellStyle = styled(NavLink)`
height: 50px;
border-radius: 4px 4px 0 0;
text-align: center;
font-size: 18px;
display: flex;
justify-content: center;
align-items: center;
 text-decoration: none;
font-weight: 400;
color: #FFFFFF;
padding: 0 30px;
margin-right: 10px;
transition: all .2s ease; 
cursor:pointer;
background: linear-gradient(0deg, #0C133F 0%, #161F57 100%);
 transform: translate(0,  5px);
 
 &.ac{
 background: #1A2460;
 transform: translate(0, 0px);
 }
@media (max-width: ${({theme}) => theme.md}){
  font-size: 12px;
 padding: 0 5px;
 margin-right: 3px;
}
`;
