import {PolarAngleAxis, PolarGrid, RadarChart, Radar} from "recharts";
import React from "react";

const data = [
    { subject: 'Math', A: 120, B: 110, fullMark: 150 },
    { subject: 'Chinese', A: 98, B: 130, fullMark: 150 },
    { subject: 'English', A: 86, B: 130, fullMark: 150 },
    { subject: 'Geography', A: 99, B: 100, fullMark: 150 },
    { subject: 'Physics', A: 85, B: 90, fullMark: 150 },
];

export const RadarChartWidget:React.FC = () => {
    return (
        <RadarChart cx={140} cy={125} outerRadius={100} width={280} height={250}data={data}>
            <PolarGrid />
            <PolarAngleAxis dataKey="subject" tick={customTick}/>

            <Radar name="Mike" dataKey="A" stroke="#8884d8" fill="#8884d8" fillOpacity={0.6}/>
        </RadarChart>
    );
}





// @ts-ignore
function customTick({ payload, x, y, textAnchor, stroke, radius }) {
    return (
        <g
            className="recharts-layer recharts-polar-angle-axis-tick"
        >
            <text
                radius={radius}
                stroke={stroke}
fill={'#fff'}
                x={x}
                y={y}
                className="recharts-text recharts-polar-angle-axis-tick-value"
                textAnchor={textAnchor}
            >
                <tspan x={x} dy="0em">
                    {payload.value}
                </tspan>
            </text>
        </g>
    );
}

