import React, {useContext} from "react";
import {useModel} from "../../../hook/use_model";
import {ConvertMoal} from "../../modal/convert_xxx";
import {ApproveButton, ButtonWidget, ImageWidget, ValueWidget} from "../../base";
import {CardCellWidget, ChooseCardStyle} from "../choose/choose_card";
import styled from "styled-components";
import {StartStyle} from "../choose/start_style";
import {CradCentext} from "../../../view/start_mining/choose_page";
import {useCardFn} from "../../../view/start_mining/fn/card_fn";
import {getBalanceNumber} from "../../../utils/formatBalance";
import {useCapitaStakeDeposit} from "../../../server/capita_stake_server";
import {useWeb3} from "../../../provider/web3_provider";
import {useAllowance} from "../../../server/token_server";


export const DashboardCard: React.FC = () => {
    const model = useContext(CradCentext)
    const {balance,amount,rewardDebt} = useCardFn(model);
    const {contrcts} = useWeb3();

    const [handleDeposit] = useCapitaStakeDeposit(model.nick);

    const [handlePresent] = useModel(
        <ConvertMoal title={model.nick}
                     url={model.url}
                     max={getBalanceNumber(balance).toFixed(3)}
                     buttonText={"Start Mining"}
                     confirmationFn={handleDeposit}/>
    );

    const allowance = useAllowance(model.contract, contrcts.capitaStakeAddress);
    return (
        <ChooseCardStyle size={{all: 30}}>
            <ImageWidget bgColor={model.bgColor} borderColor={model.bgColor} size={70} url={model.url}/>
            <h3>{model.nick}</h3>

            <CardCellWidget label={`n${model.nick}`}>
                <ValueWidget value={getBalanceNumber(balance)} decimals={3}/>
            </CardCellWidget>

            <CardCellWidget label={"My Staking"}>
                <ValueWidget value={getBalanceNumber(amount)} decimals={3}/>
            </CardCellWidget>
            <CardCellWidget label={"My Reward"}>
                <ValueWidget value={getBalanceNumber(rewardDebt)} decimals={3}/>

                {/*<span>{model.total} Nsure</span>*/}
            </CardCellWidget>

            <ButtonStyle>
                {model.contract ? allowance <= 0 ?
                    <ApproveButton contract={model.contract} contractAddress={model.address || ''}
                                   spender={contrcts.capitaStakeAddress}/>
                    : <ButtonWidget text="Claim Reward" onClick={handlePresent} type="img"/> :
                    <ButtonWidget text={`Under Development`} type="img"/>
                }
            </ButtonStyle>
        </ChooseCardStyle>
    );
}


const ButtonStyle = styled.div`
  ${StartStyle.ButtonStyle};
  margin-top: 19px;
`
