import {useWeb3} from "../provider/web3_provider";
import {useWallet} from "use-wallet";
import {useSendFn} from "../utils/web3_send";
import {useCallback} from "react";
import {HttpServer} from "../net/http_server";


export const useBuyInsurance = () => {
    const {contrcts, web3} = useWeb3();
    const handleSendFn = useSendFn(contrcts.buyAddress);
    const facethBack = useCallback(async (data: any) => {
        let {address, cover, cost, days, v, r, s, deadline} = data;
        let method = contrcts.buyContract.methods.buyInsuranceWithETH(
            web3.utils.toChecksumAddress(address),
            cover,
            cover,
            days,
            v,
            r,
            s,
            deadline,
        );
        let txHash = await handleSendFn(method);
        return txHash.transactionHash
    }, [
        contrcts,
        HttpServer.account,
        web3
    ])
    return [facethBack]
}