import NetAxios from './api'


export class HttpServer {
    static account: string | null;

    /**
     * product
     * @param address
     */
    static product(address: string): Promise<any> {
        return NetAxios.get('/quote', {product: address});
    }


    /**
     * sing
     * @param account
     * @param type
     * @param amount
     * @param period
     * @param product
     * @param nonce
     */
    static sign(account: string, type: string, amount: number, period: number, product: string, nonce: string): Promise<any> {
        return NetAxios.post('/sign', {
            product,
            amount,
            "cost": amount,
            type,
            period,
            nonce,
            account,
            'deadline': 1607680924304
        });
    }


    /**
     * stakeBalance
     * @param user
     * @param balance
     */
    static stakeBalance(balance: string) {
        return NetAxios.get('/stake/balance', {
            user: this.account, balance
        });
    }

    /**
     * stake
     * @param sign
     * @param user
     * @param params
     */
    static stake(sign: string, params: string) {
        return NetAxios.post('/stake/stake', {
            user: this.account, sign, params
        });
    }


    /**
     * unstakeAndclaimAndwithdraw
     * @param url
     * @param account
     * @param nonce
     * @param amount
     * @param currency
     */
    static unstakeAndclaimAndwithdraw(url:string,account:string,nonce: string, amount: string, currency: string|null):Promise<any> {
        return NetAxios.post(url, {
            account, amount, nonce, currency
        })
    }

}