import React from "react";
import styled from "styled-components";
import {List, MenuItem, Select} from "@material-ui/core";
import {useWeb3} from "../../../provider/web3_provider";




export const SelectWidget: React.FC<{
    handleChange: (ev: React.ChangeEvent<{ value: unknown }>) => void,
    value: unknown,
    selectMap:string[]
}> = ({handleChange, value,selectMap}) => {


    return (<SelectStyle
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        value={value}
        onChange={handleChange}
        MenuProps={{
            anchorOrigin: {
                vertical: "bottom",
                horizontal: "left"
            },
            transformOrigin: {
                vertical: "top",
                horizontal: "left"
            },
            getContentAnchorEl: null
        }}

    >
        {

            selectMap.map((ev)=>  <MenuItem key={ev} value={ev}>{ev}</MenuItem>)
        }
    </SelectStyle>);
}


const SelectStyle = styled(Select)`
margin-left: 10px;
background-color: transparent;
.MuiSelect-icon{
color: #7688B7;
}
.MuiInputBase-input{
//color: #7688B7;
}
`
