import React, {useContext} from 'react';
import styled from 'styled-components';
import {CardStyle} from '../../widget/style/card_style';
import {BorderWidget} from '../../widget/base/border_widget';
import {SpacingWidget} from '../../widget/base/spacing_widget';
import {Grid} from '@material-ui/core';
import {ButtonWidget, ValueWidget} from "../../widget/base";
import {SearchListWidget} from "../../widget/search_list/search_list";
import {formatAddress, getBalanceNumber} from "../../utils/formatBalance";
import {StartContext} from "./index";
import {useModel} from "../../hook/use_model";
import {ConvertMoal} from "../../widget/modal/convert_xxx";
import {useUnStakeAndWithdraw} from "./fn/unstake";
import {HttpServer} from "../../net/http_server";
import {useWeb3} from "../../provider/web3_provider";


export const StartManagePage: React.FC = () => {
    const {contrcts} = useWeb3();
    const {staked, reward, withdraw} = useContext(StartContext);


    const handleUnStake = useUnStakeAndWithdraw('/stake/unstake',contrcts.stakeContract.methods.unstake);
    const [onUnStake] = useModel(<ConvertMoal
        buttonText={"Unstaked"}
        max={getBalanceNumber(staked)}
        url={''}
        title={"Unstaked"}
        confirmationFn={handleUnStake}
    />);

    const handleWithdraw = useUnStakeAndWithdraw('/stake/withdraw',contrcts.stakeContract.methods.withdraw);

    const [onWithdraw] = useModel(<ConvertMoal
        buttonText={"Withdraw"}
        max={getBalanceNumber(staked)}
        url={''}
        title={"Withdraw"}
        confirmationFn={handleWithdraw}
    />);

    const handleClaim = useUnStakeAndWithdraw('/stake/claim',contrcts.stakeContract.methods.claim);

    const [onClaim1] = useModel(<ConvertMoal
        buttonText={"onClaim1"}
        max={getBalanceNumber(staked)}
        url={''}
        title={"onClaim1"}
        confirmationFn={(ev) => handleClaim(ev,'1')}
    />);

    const [onClaim2] = useModel(<ConvertMoal
        buttonText={"onClaim2"}
        max={getBalanceNumber(staked)}
        url={''}
        title={"onClaim2"}
        confirmationFn={(ev) => handleClaim(ev,'2')}
    />);




    return (
        <div>
            <WapStyle>
                <BorderWidget y={24}>
                    <h2>Your Holdings</h2>
                </BorderWidget>
                <SubTitleStyle container>
                    <Grid item md={12} sm={12} xs={12} container>
                        <Grid item md={4} sm={4} xs={4}>
                            <MsgItemWidgte value={getBalanceNumber(staked)} label={"Nsure Staked"}>
                                <ButtonWidget text={"Unstaked"} onClick={onUnStake} type={"img"}/>
                            </MsgItemWidgte>
                        </Grid>
                        <Grid item md={4} sm={4} xs={4}>
                            <MsgItemWidgte value={getBalanceNumber(reward)} label={"My Reward"}>
                                <ButtonWidget text={"Claim Reward"} type={"img"} onClick={onClaim1}/>
                                <ButtonWidget text={"Claim Reward"} type={"img"} onClick={onClaim2}/>
                            </MsgItemWidgte>
                        </Grid>
                        <Grid item md={4} sm={4} xs={4}>
                            <MsgItemWidgte value={getBalanceNumber(withdraw)} label={"Nsure can be withdrawed"}>
                                <ButtonWidget text={"Withdraw"} onClick={onWithdraw} type={"img"}/>
                            </MsgItemWidgte>
                        </Grid>
                    </Grid>
                </SubTitleStyle>

            </WapStyle>
            <SpacingWidget/>
            <WapStyle>
                <BottomWidget/>
            </WapStyle>
        </div>
    );
}


export const BottomWidget: React.FC = () => {
    return (
        <>
            <SearchListWidget title={"My Portfolio List"}
                              url={'/stake/list'}
                              isShowSearch={false}
                              sub={`Tips: The staking value for one specified contract is up to the relative factors of them,you can get more info from here: ..`}
                              structure={(ev) => [

                                  {
                                      inner: formatAddress(ev.product),
                                      color: "#FFFFFF",
                                  },
                                  {
                                      inner: ev.amountShow,
                                      color: "#FFFFFF",
                                  },
                                  {
                                      inner: `123`,
                                      color: "#FFFFFF",
                                  },
                                  {
                                      inner: 'Claim',
                                      color: "#FFFFFF",
                                      textAlign: "center"

                                  }
                              ]}>
                <div>Products</div>
                <div>Active Cover Amount</div>
                <div>Staked Amount</div>
                <div>Unstake Amount</div>
            </SearchListWidget>

            <ButtonStyle>
                <ButtonWidget text={'Unstake'} type={"img"} />
            </ButtonStyle>
        </>
    );
}

const ButtonStyle = styled.div`
    display: flex;
  padding: 30px;
  box-sizing: border-box;
  justify-content: flex-end;
  
`


const MsgItemWidgte: React.FC<{ label: string, value: number | string }> = ({label, value = 0, children}) => {
    return (
        <MsgItemStyle>
            <h2>{label}</h2>
            <h3><span>{getBalanceNumber(value)} Nsure</span></h3>
            <div className={'button_img'}>
                {children}
            </div>
        </MsgItemStyle>
    );
}

